/*选择按钮*/

input[type="radio"] + label::before {
    content: "\a0"; /*不换行空格*/
    display: inline-block;
    vertical-align: middle;
    font-size: 14px;
    width: 1em;
    height: 1em;
    margin-right: .4em;
    border-radius: 50%;
    border: 1px solid #1890ff;;
    text-indent: .15em;
    line-height: 1;
    cursor: pointer;
}

input[type="radio"]:checked + label::before {
    background-color: #1890ff;;
    background-clip: content-box;
    padding: .15em;
    border-color:#1890ff;
}
input[type="radio"] {
    position: absolute;
    clip: rect(0, 0, 0, 0);
}
